import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

ReactDOM.render(<App />, document.getElementById('root'));
window.addEventListener('beforeinstallprompt', async (e) => {
    console.log('beforeinstallprompt event has fired')
    
    const prompted = await e.prompt()
    console.log('user got prompted')
    console.log('responded with', prompted)
    // .then(()=> console.log('user was prompted'))
    // .catch(err => console.log('user wasnt prompted becaues:', err))
    
})

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register();
